<template>
  <el-form
    class="form"
    :inline="true"
    @submit.native.prevent="onSubmit"
    :model="form"
    label-width="100px"
  >
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/notice">消息列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24" v-loading="loading">
      <el-col>
        <el-form-item label="名称">
          <el-input v-model="form.name" placeholder="请输入名称"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="vip等级">
          <el-select v-model="form.vip_id" clearable placeholder="请选择">
            <el-option
              v-for="(value, key) in config.vip"
              :key="key"
              :label="value.name"
              :value="value.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="内容" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'
import { VueEditor } from 'vue2-editor'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      loading: false,
      form: {
        title: '',
        content: '',
        type: 'vip',
        vip_id: '',
      },
      config: [],
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    this.loading = true
    Fetch('/user/notice/config')
      .then(r => {
        this.config = r.data
        this.loading = false
      })
      .catch(() => {
        this.loading = false
      })
  },
  methods: {
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      this.loading = true
      const id = this.$router.history.current.params.id
      Fetch('/user/notice/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
          this.$router.go(-1)
        })
        .catch(() => {
          Message.error('操作失败')
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
